import * as React from "react";
import {
  FunctionComponent,
  useState,
  useMemo,
  type CSSProperties,
  useCallback,
} from "react";
import MatterhornPopup from "./matterhorn-popup";
import PortalPopup from "./portal-popup";
import * as styles from "./popular-stays-hotel-card.module.css";

type PopularStaysHotelCardType = {
  imageDimensions?: string;
  accommodationType?: string;
  accommodationName?: string;
  pricePerNight?: string;
  imageDimensionsText?: string;
  imageDimensionsText2?: string;
  rating?: string;
  reviewCountText?: string;

  /** Style props */
  propFlex?: CSSProperties["flex"];
  propGap?: CSSProperties["gap"];
};

const PopularStaysHotelCard: FunctionComponent<PopularStaysHotelCardType> = ({
  imageDimensions,
  accommodationType,
  accommodationName,
  pricePerNight,
  imageDimensionsText,
  imageDimensionsText2,
  rating,
  reviewCountText,
  propFlex,
  propGap,
}) => {
  const [isMatterhornPopupOpen, setMatterhornPopupOpen] = useState(false);
  const stayDetailsStyle: CSSProperties = useMemo(() => {
    return {
      flex: propFlex,
      gap: propGap,
    };
  }, [propFlex, propGap]);

  const openMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(true);
  }, []);

  const closeMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(false);
  }, []);

  return (
    <>
      <div className={styles.hotelCard1}>
        <div className={styles.hotelCard}>
          <img
            className={styles.matterhornSuitesImage}
            alt=""
            src={imageDimensions}
          />
          <div className={styles.stayDetails} style={stayDetailsStyle}>
            <div className={styles.stayDetailsRows}>
              <div className={styles.entireBungalow}>{accommodationType}</div>
              <b className={styles.matterhornSuites}>{accommodationName}</b>
              <div className={styles.night}>{pricePerNight}</div>
            </div>
            <img
              className={styles.videoIcon}
              alt=""
              src={imageDimensionsText}
              onClick={openMatterhornPopup}
            />
          </div>
          <div className={styles.rating}>
            <div className={styles.vectorParent}>
              <img
                className={styles.vectorIcon}
                alt=""
                src={imageDimensionsText2}
              />
              <div className={styles.div}>{rating}</div>
            </div>
            <div className={styles.reviews}>{reviewCountText}</div>
          </div>
          <button className={styles.moreDetailsButton}>
            <div className={styles.button}>More details</div>
          </button>
        </div>
      </div>
      {isMatterhornPopupOpen && (
        <PortalPopup
          overlayColor="rgba(0, 0, 0, 0.3)"
          placement="Centered"
          onOutsideClick={closeMatterhornPopup}
        >
          <MatterhornPopup onClose={closeMatterhornPopup} />
        </PortalPopup>
      )}
    </>
  );
};

export default PopularStaysHotelCard;
